.main-nav {
  position: relative;
  padding: 10px 8px 5px 20px;
  background-color: $color-primary;
}

.main-nav__toggle {
  position: relative;
  left: 12px;
  display: block;
  width: 24px;
  height: 27px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: $color-white;
  box-shadow: 0 5px 0 0 $color-white, 0 10px 0 0 $color-white;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav.main-nav--opened.main-nav--closed {
  padding-bottom: 0px;
}

.main-nav--opened .main-nav__toggle {
  top: 2px;
  right: 28px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 12px;
    left: 2px;
    width: 20px;
    height: 2px;
    background-color: $color-white;
  }

  &::before {
    transform: rotate(43deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-43deg);
  }
}

.main-nav--closed .menu__item,
.user-menu--closed .user-menu__item {
  display: none;
}

.main-nav--opened .menu__item,
.user-menu--opened .user-menu__item {
  display: block;
}

@media (min-width: $screen-width-S) {
  .main-nav {
    padding: 0 calc(50% - 350px);
  }

  .main-nav__toggle {
    width: 48px;
    height: 42px;
    margin: 0;
    margin-left: 37px;
  }

  .main-nav--closed .main-nav__toggle::before {
    top: 11px;
    left: 0;
    width: 46px;
    height: 4px;
    box-shadow: 0 9px 0 0 $color-white, 0 18px 0 0 $color-white;
  }

  .main-nav--opened .main-nav__toggle {
    &::before,
    &::after {
      top: 18px;
      width: 46px;
      height: 4px;
    }

    &::before {
      box-shadow: none;
    }

  }
}

@media (min-width: $screen-width-M) {
  .main-nav  {
    padding: 0 calc(50% - 480px);
  }
}

@media (min-width: $screen-width-L){
  .main-nav  {
    padding: 0 calc(50% - 590px);
  }
}
